<template>
  <div id="liquidFill">
    <div :id="domId" style="height: 110px; width: 110px"></div>
    <div>{{ dataI.title }}</div>
    <div>在线数：xxxxxx</div>
    <div>离线数：xxxxxx</div>
  </div>
</template>

<script>
export default {
  props: ['domId', 'dataI'],
  mounted() {
    const { count } = this.dataI;
    function Pie() {
      let dataArr = [];
      for (var i = 0; i < 150; i++) {
        if (i % 2 === 0) {
          dataArr.push({
            name: (i + 1).toString(),
            // value: 25,
            value: 15,
            itemStyle: {
              normal: {
                color: '#3192d8',
                borderWidth: 0,
                borderColor: 'rgba(0,0,0,0)'
              }
            }
          });
        } else {
          dataArr.push({
            name: (i + 1).toString(),
            value: 10,
            itemStyle: {
              normal: {
                color: 'rgba(0,0,0,0)',
                borderWidth: 0,
                borderColor: 'rgba(0,0,0,0)'
              }
            }
          });
        }
      }
      return dataArr;
    }

    const myChart = echarts.init(document.getElementById(this.domId));
    // 绘制图表
    const option = {
      // backgroundColor: 'black',
      title: {
        top: '40%',
        left: 'center',
        text: count + '%',
        textStyle: {
          color: 'rgba(255, 255, 255, 255.45)',
          fontWeight: 'normal',
          fontSize: 14
        }
        // subtext: '123',
        // subtextStyle: {
        //   color: 'rgba(255, 255, 255, 0.85)',
        //   fontSize: 18
        // }
      },
      series: [
        {
          type: 'liquidFill',
          radius: '80%',
          data: [count / 100, (count - 10) / 100],
          backgroundStyle: {
            color: {
              type: 'radial',
              x: 0.5,
              y: 0.5,
              r: 0.5,
              colorStops: [
                {
                  offset: 0,
                  color: 'rgba(0,24,55, 0)'
                },
                {
                  offset: 0.75,
                  color: 'rgba(0,24,55, 0)'
                },
                {
                  offset: 1,
                  color: 'rgba(37,237,259, 1)'
                }
              ],
              globalCoord: false
            }
          },
          outline: {
            borderDistance: 0,
            itemStyle: {
              borderWidth: 0,
              borderColor: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: 'rgba(50,115,256, 1)'
                  },
                  {
                    offset: 0.5,
                    color: 'rgba(50,115,233, .75)'
                  },
                  {
                    offset: 1,
                    color: 'rgba(50,115,233, 1)'
                  }
                ],
                globalCoord: false
              }
            }
          },
          // color: ['rgba(37,237,240, 1)', 'rgba(37,200,220, 1)'],
          color: {
            type: 'linear',
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops: [
              {
                offset: 0,
                color: 'rgba(37,237,259, 1)'
              },
              {
                offset: 0.5,
                color: 'rgba(0,100,259, 0.5)'
              },
              {
                offset: 1,
                color: 'darkblue'
              }
            ],
            globalCoord: false
          },
          label: {
            normal: {
              formatter: ''
            }
          }
        },
        {
          type: 'pie',
          zlevel: 0,
          silent: true,
          radius: ['91%', '95%'],
          z: 10,
          itemStyle: {
            normal: {
              areaColor: 'rgba(49, 146, 216, .3)',
              borderColor: 'rgba(49, 146, 216, 1)'
            },
            emphasis: {
              label: {
                show: false
              },
              areaColor: 'rgba(49, 146, 216, 1)'
            }
          },
          label: {
            normal: {
              show: false
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: Pie()
        },
        {
          type: 'pie',
          startAngle: 225 - count,
          radius: ['90%', '97%'],
          clockwise: false,
          avoidLabelOverlap: false,
          hoverOffset: 0,
          label: {
            show: false
          },
          labelLine: {
            show: false
          },
          itemStyle: {
            normal: {
              color: function (params) {
                return ['#3192d8', 'rgba(0,0,0,0)'][params.dataIndex];
              }
            }
          },
          data: [count, 100 - count]
        }
      ]
    };
    myChart.setOption(option);
  }
};
</script>

<style lang="scss" scoped>
#liquidFill {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  div {
    margin-bottom: 3px;
  }
}
</style>
