<template>
  <div class="box">
    <!-- <div :id="pieId" class="pie"></div> -->
    <!-- npm安装的echarts和index引入的有冲突 -->
    <pie3d :id="pieId" class="pie"></pie3d>
    <div :id="circleId" class="circle"></div>
  </div>
</template>

<script>

import pie3d from './pie3dEcharts.vue'
export default {
  components:{pie3d},
  props: ['pieId', 'circleId', 'dataI'],
  
  mounted() {
  
    // this.initPie()
    this.initCircle()
    
    
    
  },
  methods: {
    initPie(){
      const { count,finished } = this.dataI;
      this.statusChart = echarts.init(document.getElementById(this.pieId))
      // 传入数据生成 option, 构建3d饼状图, 参数工具文件已经备注的很详细
      this.option = getPie3D(this.optionData, 0.8, 240, 28, 26, 0.5)
      this.statusChart.setOption(this.option)
      // 是否需要label指引线，如果要就添加一个透明的2d饼状图并调整角度使得labelLine和3d的饼状图对齐，并再次setOption
      this.option.series.push({
        name: '电梯状态', //自己根据场景修改
        backgroundColor: 'transparent',
        type: 'pie',
        label: {
          opacity: 1,
          fontSize: 13,
          lineHeight: 20
        },
        startAngle: -40, // 起始角度，支持范围[0, 360]。
        clockwise: false, // 饼图的扇区是否是顺时针排布。上述这两项配置主要是为了对齐3d的样式
        radius: ['20%', '50%'],
        center: ['50%', '50%'],
        data: this.optionData,
        itemStyle: {
          opacity: 0  //这里必须是0，不然2d的图会覆盖在表面
        }
      })
      this.statusChart.setOption(this.option)
      // this.bindListen(this.statusChart)
    },
    initCircle() {
      const { count } = this.dataI;
      function Pie() {
        let dataArr = [];
        for (var i = 0; i < 150; i++) {
          if (i % 2 === 0) {
            dataArr.push({
              name: (i + 1).toString(),
              // value: 25,
              value: 15,
              itemStyle: {
                normal: {
                  color: '#3192d8',
                  borderWidth: 0,
                  borderColor: 'rgba(0,0,0,0)'
                }
              }
            });
          } else {
            dataArr.push({
              name: (i + 1).toString(),
              value: 10,
              itemStyle: {
                normal: {
                  color: 'rgba(0,0,0,0)',
                  borderWidth: 0,
                  borderColor: 'rgba(0,0,0,0)'
                }
              }
            });
          }
        }
        return dataArr;
      }

      const myChart = echarts.init(document.getElementById(this.circleId));
      // 绘制图表
      const option = {
        // backgroundColor: 'black',
        title: {
          top: '40%',
          left: 'center',
          text: count + '% \n完成率',
          textStyle: {
            color: 'rgba(255, 255, 255, 255.45)',
            fontWeight: 'normal',
            fontSize: 14
          }
          // subtext: '123',
          // subtextStyle: {
          //   color: 'rgba(255, 255, 255, 0.85)',
          //   fontSize: 18
          // }
        },
        series: [
          {
            type: 'liquidFill',
            radius: '80%',
            data: [count / 100, (count - 10) / 100],
            backgroundStyle: {
              color: {
                type: 'radial',
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [
                  {
                    offset: 0,
                    color: 'rgba(0,24,55, 0)'
                  },
                  {
                    offset: 0.75,
                    color: 'rgba(0,24,55, 0)'
                  },
                  {
                    offset: 1,
                    color: 'rgba(37,237,259, 1)'
                  }
                ],
                globalCoord: false
              }
            },
            outline: {
              borderDistance: 0,
              itemStyle: {
                borderWidth: 0,
                borderColor: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    {
                      offset: 0,
                      color: 'rgba(50,115,256, 1)'
                    },
                    {
                      offset: 0.5,
                      color: 'rgba(50,115,233, .75)'
                    },
                    {
                      offset: 1,
                      color: 'rgba(50,115,233, 1)'
                    }
                  ],
                  globalCoord: false
                }
              }
            },
            // color: ['rgba(37,237,240, 1)', 'rgba(37,200,220, 1)'],
            color: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                {
                  offset: 0,
                  color: 'rgba(37,237,259, 1)'
                },
                {
                  offset: 0.5,
                  color: 'rgba(0,100,259, 0.5)'
                },
                {
                  offset: 1,
                  color: 'darkblue'
                }
              ],
              globalCoord: false
            },
            label: {
              normal: {
                formatter: ''
              }
            }
          },
          {
            type: 'pie',
            zlevel: 0,
            silent: true,
            radius: ['91%', '95%'],
            z: 10,
            itemStyle: {
              normal: {
                areaColor: 'rgba(49, 146, 216, .3)',
                borderColor: 'rgba(49, 146, 216, 1)'
              },
              emphasis: {
                label: {
                  show: false
                },
                areaColor: 'rgba(49, 146, 216, 1)'
              }
            },
            label: {
              normal: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: Pie()
          },
          {
            type: 'pie',
            startAngle: 225 - count,
            radius: ['90%', '97%'],
            clockwise: false,
            avoidLabelOverlap: false,
            hoverOffset: 0,
            label: {
              show: false
            },
            labelLine: {
              show: false
            },
            itemStyle: {
              normal: {
                color: function (params) {
                  return ['#3192d8', 'rgba(0,0,0,0)'][params.dataIndex];
                }
              }
            },
            data: [count, 100 - count]
          }
        ]
      };
      myChart.setOption(option);
    }
  }
};
</script>

