<template>
  <div id="equipmentClassification">
    <div class="title">
      <span @click="changeData(1)">设备台账</span>
      <span @click="changeData(2)">临时设备</span>
    </div>
    <equipmentClassificationEcharts
      ref="echartsRef"
      :echartsData="echartsData"
      :echartsValue="echartsValue"
    ></equipmentClassificationEcharts>
  </div>
</template>

<script>
import equipmentClassificationEcharts from '@/components/echarts/equipmentClassificationEcharts';
export default {
  components: { equipmentClassificationEcharts },
  data() {
    return {
      echartsData: ['终端设备', '网络设备', '主机设备', '', '', '', '', ''],
      echartsValue: [1246, 1167, 1148, 861, 500, 322, 310, 247]
    };
  },
  methods: {
    changeData(num) {
      if (num == 1) {
        this.echartsValue = [1246, 1167, 1148, 861, 500, 322, 310, 247];
        setTimeout(() => {
          this.$refs['echartsRef'].goCharts();
        }, 100);
      } else {
        this.echartsValue = [1300, 1200, 1111, 888, 555, 333, 333, 222];
        setTimeout(() => {
          this.$refs['echartsRef'].goCharts();
        }, 100);
      }
    }
  }
};
</script>

