<template>
  <div id="equipmentPatrol">
    <div class="echarts">
      <equipmentPatrolEcharts
        class="echartsbox"
        v-for="(item, index) in dataArr"
        :pieId="`pie${index}`"
        :circleId="`circleId${index}`"
        :dataI="item"
        :key="index"
      ></equipmentPatrolEcharts>
    </div>
    <div id="ipResource">
    <div class="ipResource-title">
      <span>工单编号</span>
      <span>巡视类型</span>
      <span>状态</span>
      <span>是否隐患</span>
    </div>
    <div class="ipResource-list">
      <ul id="swiperWrap" style="height: calc(100%-40px)" @mousemove="mousemoveIn" @mouseleave="goSwiper">
        <li>
          <span>xs20005555</span>
          <span>日常巡视</span>
          <span>进行中</span>
          <span>是</span>
        </li>
        <li>
          <span>xs20005555</span>
          <span>日常巡视</span>
          <span>进行中</span>
          <span>是</span>
        </li>
      </ul>
    </div>
  </div>
  </div>
</template>

<script>
import equipmentPatrolEcharts from '../echarts/equipmentPatrolEcharts'
export default {
  components:{equipmentPatrolEcharts},
  data() {
    return {
      swiperTime: null,
      cur: 0,
      dataArr:[
        {
          title:'日常巡视',
          finished:3,
          count:17,

        },
        {
          title:'计划巡视',
          finished:3,
          count:17,

        },
        {
          title:'自动化巡视',
          finished:3,
          count:17,

        }
      ]
    };
  },
  mounted() {
    this.goSwiper();
  },

  methods: {
    goSwiper() {
      const _this = this;
      const ele = document.getElementById('swiperWrap'); // ul 要把这个往上滑。
      const listDom = ele.getElementsByTagName('li');
      const list = [...listDom]; // 拿到的li数组
      const len = list.length;

      if (len > 5) {
        // 列表项大于5时，才做swiper动画。
        // list.push(list[0]); // 这里是模拟，在dom元素的末尾，添加列表第一项，真实需要自己做dom操作。
        const swpier = function (delay) {
          _this.swiperTime = setTimeout(function () {
            _this.cur += 1;
            // console.log(cur, len);
            // if (len - cur == 6) {
            if (_this.cur > len) {
              // 这时表示到了最后一个复制块，直接无动画移动到真实的第一个元素
              _this.cur = 0;
              // ele.style.transition = 'none';
              ele.style.transform = 'translateY(0)';
              ele.style.transition = 'all 1s';
              swpier(1000);
            } else {
              const top = -27.094 * _this.cur;
              ele.style.transform = 'translateY(' + top + 'px)';
              ele.style.transition = 'all 1s';
              _this.cur === len ? swpier(1000) : swpier(2000); // 如果是最后一个复制块，间隔时间需要修改，加上最上面的替换块，合计2s。
            }
          }, delay);
        };
        swpier(1000);
      }
    },
    mousemoveIn() {
      clearTimeout(this.swiperTime);
    }
  }
};
</script>

