<template>
  <div class="orderBarChart">
    <div id="orderBar"></div>
  </div>
</template>

<script>
export default {
  props: ['barData'],
  data() {
    return{

    }
  },
  mounted() {
    this.initChart()
  },
  methods: {
    // 初始化图表
    initChart() {
      const myChart = echarts.init(document.getElementById('orderBar'))
      const option = {
        color: ['#00baff'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          },
        },
        grid: {
          left: '-7%',
          right: '0%',
          bottom: '3%',
          top: '10%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: this.barData.xData,
          axisLine: {
            show: false, // 不显示坐标轴线
          },
          axisLabel: {//x轴文字的配置
            show: true,
            textStyle: {
                color: "#fff",
            }
          }
        },
        yAxis: {
          type: 'value',
          show:false
        },
        series: [
          {
            data: this.barData.yData,
            type: 'bar',
            barWidth: '30',
            showBackground: true,
            backgroundStyle: {
              color: 'rgba(180, 180, 180, 0.2)'
            },
            label: {
              show: true,
              position: 'inside'
            },
          }
        ]
      }
      myChart.setOption(option);
    },
  },
}
</script>

<style lang="scss" scoped>
.orderBarChart{
  height: 100%;
  #orderBar{
    width: 75%;
    height: 100%;
  }
}
</style>