<template>
  <div id="app" :style="resizeClass">
    <page />
  </div>
</template>

<script>
import page from './components/page';
export default {
  name: 'App',
  components: {
    page
  },
  data() {
    return {
      resizeClass: {}
    };
  },
  created() {
    window.addEventListener('resize', this.resize);
    this.resize();
  },
  methods: {
    resize() {
      var ratioX = window.innerWidth / 1920;
      var ratioY = window.innerHeight / 937;
      this.resizeClass = {
        transform: 'scale(' + ratioX + ',' + ratioY + ')',
        transformOrigin: 'left top',
        backgroundSize: '100% 100%'
      };
      // $('html').css({ overflow: 'hidden' });
    }
  }
};
</script>

<style lang="scss">
#app {
  position: fixed;
  height: 937px;
  width: 1920px;
}
</style>
