        { data1: '编号9', data2: '详细信息9', data3: '2022-12-06', data4: 'ok', data5: '发起人', data6: '受理人' }
      ],
      tableInterval: null // 表格滚动定时器
    };
  },
  mounted() {
    this.tableScroll();
  },
  methods: {
    // 表格数据滚动
    tableScroll() {
      const table = this.$refs.workTaskTable;
      const divData = table.bodyWrapper;
      if (this.tableData.length > 4) {
        this.tableInterval = setInterval(() => {
          divData.scrollTop += 1;
          if (parseInt(divData.clientHeight + divData.scrollTop) == divData.scrollHeight) {
            divData.scrollTop = 0;
          }
        }, 30);
      }
    },
    // 鼠标滑入表格
    mouseEnter() {
      clearInterval(this.tableInterval);
      this.tableInterval = null;
    },
    // 鼠标离开表格
    mouseLeave() {
      this.tableScroll();
    }
  }
};
</script>

